.barra {
	display: flex;
	flex-direction: column;
	padding-top: 2.5rem;

	@include tablet() {
		flex-direction: row;
		justify-content: space-between;
	}
}

.navegacion {
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 1.8rem;
	a {
		color: $blanco;
		text-decoration: none;

		text-align: center;
	}

	@include tablet() {
		flex-direction: row;
		gap: 2rem;
	}
}
